import React from 'react';
import moment from 'moment';

const DateInfo = () => {
  const day = moment().format('dddd');
  const date = moment().format('YYYY-MM-DD');

  return (
    <div className="DateInfo">
      <div className="day">
        {day === 'Monday' && ''}
        {day === 'Tuesday' && ''}
        {day === 'Wednesday' && ''}
        {day === 'Thursday' && ''}
        {day === 'Friday' && ''}
        {day === 'Saturday' && ''}
        {day === 'Sunday' && ''}
      </div>
      <div className="date">{date}</div>
    </div>
  );
};

export default DateInfo;



