import React, { Component } from 'react';
import { useTodo } from '../../contexts/todo';

class Send extends Component {
  state = {
    todoText: ''
  };

  handleChange = e => {
    this.setState({
      todoText: e.target.value
    });
  };

  handleSubmit = e => {
    e.preventDefault();

    if (this.state.todoText === '') {
      this.state.todoText === '' && alert('  .');
      e.target.todo.focus();
    } else {
      this.props.onCreate(this.state);
      this.setState({
        todoText: ''
      });
      e.target.todo.value = '';
    }
  };

  render() {
    return (
      <form onSubmit={this.handleSubmit} className="todo_form">
        <input
          type="text"
          placeholder="  ."
          name="todo"
          onChange={this.handleChange}
        />
        <button></button>
      </form>
    );
  }
}

export default useTodo(Send);



