import React from 'react';
import SendForm from './SendForm';
import ColorSetList from './ColorSetList';
import InfoList from './InfoList';

import './TodoFrame.css';

const TodoFrame = () => {
  return (
    <div className="TodoFrame">
      <SendForm />
      <ColorSetList />
      <InfoList />
    </div>
  );
};

export default TodoFrame;



