import React, { Component, createContext } from 'react';

const Context = createContext();

const { Provider, Consumer: OutsiderConsumer } = Context;

class OutsiderProvider extends Component {
  state = {
    act: false,
    value: 0,
    place: ''
  };

  actions = {
    handleCreate: data => {
      this.setState({
        act: true,
        ...data
      });
    },
    handleValue: value => {
      this.setState({
        value: value
      });
    },
    handlePlace: place => {
      this.setState({
        place: place
      });
    },
    handleClose: e => {
      this.setState({
        act: false,
        value: 0,
        place: ''
      });
    }
  };

  render() {
    const { state, actions } = this;
    const value = { state, actions };
    return <Provider value={value}>{this.props.children}</Provider>;
  }
}

function useOutsider(WrappedComponent) {
  return function UseTodo(props) {
    return (
      <OutsiderConsumer>
        {({ state, actions }) => (
          <WrappedComponent
            dataTime={state}
            onCreate={actions.handleCreate}
            onValue={actions.handleValue}
            onPlace={actions.handlePlace}
            onClose={actions.handleClose}
          />
        )}
      </OutsiderConsumer>
    );
  };
}

export { OutsiderProvider, OutsiderConsumer, useOutsider };



