import React from 'react';
import moment from 'moment';
import Countdown from 'react-countdown-moment';

const EmptInfo = ({ timePlace, timeValue, onClose }) => {
  const endDate = moment().add(`${timeValue}`, 'minute');

  return (
    <div className="EmptInfo">
      <div className="p">
          <span>'{timePlace}'</span>
      </div>
      <div className="t">
        <Countdown endDate={endDate} />
      </div>
      <div className="s">
        <span>{timeValue}</span>  .
      </div>
      <div className="b">
        <button type="button" onClick={onClose}>
           
        </button>
      </div>
    </div>
  );
};

export default EmptInfo;



