import React, { Component } from 'react';
import TimepickerForm from './components/timepicker/TimepickerForm';
import TodoFrame from './components/todo/TodoFrame';
import OutsiderFrame from './components/outside/OutsiderFrame';
import { TodoProvider } from './contexts/todo';
import { TimeProvider } from './contexts/timepicker';
import { OutsiderProvider } from './contexts/outsider';
import './App.css';

const AppProvider = ({ contexts, children }) =>
  contexts.reduce(
    (prev, context) =>
      React.createElement(context, {
        children: prev
      }),
    children
  );

class App extends Component {
  render() {
    return (
      <AppProvider contexts={[TodoProvider, TimeProvider, OutsiderProvider]}>
        <div className="App">
          <div className="App-left">
            <TimepickerForm />
          </div>
          <div className="App-right">
            <div className="App-right-top">
              <TodoFrame />
            </div>
            <div className="App-right-bottom">
              <OutsiderFrame />
            </div>
          </div>
        </div>
      </AppProvider>
    );
  }
}

export default App;



