import React from 'react';
import EmptInfo from './EmptInfo';
import { useOutsider } from '../../contexts/outsider';

const EmptList = ({ dataTime, onClose }) => {
  return (
    <div className={`EmptList ${dataTime.act && 'act'}`}>
      <div className="right" />
      <div className="top" />
      <div className="bottom">
        <EmptInfo
          timePlace={dataTime.place}
          timeValue={dataTime.value}
          onClose={onClose}
        />
      </div>
    </div>
  );
};

export default useOutsider(EmptList);



