import React from 'react';
import Info from './Info';
import { useTodo } from '../../contexts/todo';

const InfoList = ({ bgcolor, checked, data, onRemove, onToggle }) => {
  const infoList = data.map(info => (
    <Info
      key={info.id}
      id={info.id}
      todoText={info.todoText}
      todoChecked={info.checked}
      onRemove={onRemove}
      onToggle={onToggle}
    />
  ));

  if (!checked) {
    return (
      <ul className={`InfoList ${bgcolor}`}>
        <li className="tac">   .</li>
      </ul>
    );
  }

  return <ul className={`InfoList ${bgcolor}`}>{infoList}</ul>;
};

export default useTodo(InfoList);



