import React from 'react';
import SendForm from './SendForm';
import EmptList from './EmptList';
import './OutsiderFrame.css';

const OutsiderFrame = () => {
  return (
    <div className="OutsiderFrame">
      <SendForm />
      <EmptList />
    </div>
  );
};

export default OutsiderFrame;



