import React, { Component, createContext } from 'react';

const Context = createContext();

const { Provider, Consumer: TodoConsumer } = Context;

class TodoProvider extends Component {
  state = {
    bgcolor: 'default',
    checked: false,
    infomation: []
  };

  actions = {
    handleCreate: data => {
      const { infomation } = this.state;

      let num = 0;
      localStorage.storage
        ? (num = JSON.parse(localStorage.storage).num + 1)
        : (num = 0);

      this.setState({
        num: num,
        checked: true,
        infomation: infomation.concat({
          id: num,
          ...data
        })
      });
    },
    handleRemove: id => {
      const { infomation } = this.state;
      if (this.state.infomation.length === 1) {
        this.setState({
          checked: false,
          infomation: infomation.filter(info => info.id !== id)
        });
      } else {
        this.setState({
          infomation: infomation.filter(info => info.id !== id)
        });
      }
    },
    handleToggle: id => {
      const { infomation } = this.state;

      //   id     .
      const index = infomation.map(info => info.id).indexOf(id);
      //findIndex IE11   . const index = infomation.findIndex(infomation => infomation.id === id);

      const selected = infomation[index]; //  
      const nextInfomation = [...infomation]; //  

      //   , checked  
      nextInfomation[index] = {
        ...selected,
        checked: !selected.checked
      };

      this.setState({
        infomation: nextInfomation
      });
    },
    handleColor: color => {
      this.setState({
        bgcolor: color
      });
    }
  };

  UNSAFE_componentWillMount() {
    const storage = localStorage.storage;
    if (storage) {
      this.setState(JSON.parse(storage));
    }
  }

  componentDidMount() {}

  componentDidUpdate(prevProps, prevState) {
    if (JSON.stringify(prevState) !== JSON.stringify(this.state)) {
      localStorage.storage = JSON.stringify(this.state);
    }
  }

  render() {
    const { state, actions } = this;
    const value = { state, actions };
    return <Provider value={value}>{this.props.children}</Provider>;
  }
}

function useTodo(WrappedComponent) {
  return function UseTodo(props) {
    return (
      <TodoConsumer>
        {({ state, actions }) => (
          <WrappedComponent
            data={state.infomation}
            bgcolor={state.bgcolor}
            checked={state.checked}
            onCreate={actions.handleCreate}
            onRemove={actions.handleRemove}
            onToggle={actions.handleToggle}
            onColor={actions.handleColor}
          />
        )}
      </TodoConsumer>
    );
  };
}

export { TodoProvider, TodoConsumer, useTodo };



