import React, { Component } from 'react';
import { useTodo } from '../../contexts/todo';

class ColorSet extends Component {
  handleColor = e => {
    const { name } = e.target;
    const { onColor } = this.props;

    onColor(name);
  };

  render() {
    return (
      <div className="ColorSet">
        <button
          type="button"
          className="default"
          name="default"
          onClick={this.handleColor}
        />
        <button
          type="button"
          className="gray"
          name="gray"
          onClick={this.handleColor}
        />
        <button
          type="button"
          className="gold"
          name="gold"
          onClick={this.handleColor}
        />
        <button
          type="button"
          className="blue"
          name="blue"
          onClick={this.handleColor}
        />
        <button
          type="button"
          className="red"
          name="red"
          onClick={this.handleColor}
        />
      </div>
    );
  }
}

export default useTodo(ColorSet);



