import React, { Component } from 'react';
import { useTime } from '../../contexts/timepicker';

class TimeInfo extends Component {
  componentDidMount() {
    setInterval(this.props.onTime, 1000);
  }

  render() {
    const { dataTime } = this.props;
    const h = dataTime.time.getHours();
    const m = dataTime.time.getMinutes();
    return (
      <div className="TimeInfo">
        <span className="am-pm">{h < 12 ? 'am' : 'pm'}</span>
        {h === 12 ? '12' : h % 12}
        <span className="call-loan">:</span>
        {m < 10 ? '0' + m : m}
      </div>
    );
  }
}

export default useTime(TimeInfo);



