import React from 'react';

const Info = ({ id, todoText, todoChecked, onRemove, onToggle }) => {
  return (
    <li>
      <a onClick={() => onToggle(id)} className={`${todoChecked && 'acted'}`}>
        {todoText}
      </a>
      <button type="button" onClick={() => onRemove(id)}>
        
      </button>
    </li>
  );
};

export default Info;



