import React, { Component } from 'react';
import TimeList from './TimeList';
import DateList from './DateList';
import { useTime } from '../../contexts/timepicker';
import './TimepickerForm.css';

class TimepickerForm extends Component {
  componentDidMount() {
    setInterval(this.props.onRandom, 10000);
  }

  render() {
    const { dataTime } = this.props;
    return (
      <div className={`TimeForm ${dataTime.animateColor}`}>
        <TimeList />
        <DateList />
      </div>
    );
  }
}

export default useTime(TimepickerForm);



