import React, { Component, createContext } from 'react';

const Context = createContext();

const { Provider, Consumer: TimeConsumer } = Context;

class TimeProvider extends Component {
  state = {
    time: new Date(),
    animateColor: ''
  };

  actions = {
    handleTime: e => {
      this.setState({
        time: new Date()
      });
    },
    handleRandom: e => {
      const random = ['skyblue', 'green', 'red', 'blue', 'orange', 'gray'];
      let ranNum = random[Math.floor(Math.random() * random.length)];
      this.setState({
        animateColor: ranNum
      });
    }
  };

  render() {
    const { state, actions } = this;
    const value = { state, actions };
    return <Provider value={value}>{this.props.children}</Provider>;
  }
}

function useTime(WrappedComponent) {
  return function UseTodo(props) {
    return (
      <TimeConsumer>
        {({ state, actions }) => (
          <WrappedComponent
            dataTime={state}
            onTime={actions.handleTime}
            onRandom={actions.handleRandom}
          />
        )}
      </TimeConsumer>
    );
  };
}

export { TimeProvider, TimeConsumer, useTime };



